*{
    margin: 0;
    padding: 0;
}

body{
    background-color: #f4f7fc;
    font-size: 0.8rem;
}

/* ------------Pagination.js css--------------- */

.pagination{
    text-align: center;
    padding: 1rem 0;
}

.pagination span{
    margin:  0.5rem;
    cursor: pointer;
}

.pageLink{
    text-decoration: none;
    color: black;
}

.pageLink:active{
    background-color: red;
}


/* ------------Individual.js css--------------- */
.container{
    max-width: 90%;
    background-color: white;
    margin: 1rem auto;
    border-radius: 10px;
    padding: 2.5rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.basicDetail{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.col{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
}

h5{
    font-weight: 700;
    margin-bottom: 0.3rem;
}

button{
    background-color: #fb3e3e;
    border: none;
    outline: none;
    color: white;
    border-radius: 10px;
    padding: 0.3rem 0.4rem;
    font-size: 0.7rem;
    min-width: fit-content;
}


/* -------------Itemdetail.js css-------------- */
.aboutItem{
    margin: 3rem 0 0;
    padding: 1rem;
    border: 1px solid #f4f7fc;
    border-radius: 10px;
    width: 95%;
    -webkit-box-shadow: 0px 0px 6px -1px rgb(222, 219, 219);
    -moz-box-shadow: 0px 0px 6px -1px rgb(222, 219, 219);
    box-shadow: 0px 0px 6px -1px rgb(222, 219, 219);
}

.detailsContainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}

.details{
    min-width: 12rem;
}

.details div{
    margin: 0.5rem 0;
}